:root {
  --jtn-idle-width: 40px;
  --jtn-base-font-size: 16px;
  --jtn-small-font-size: 12.5px;
  --jtn-ff-primary: "Helvetica Neue", Helvetica, Arial, sans-serif;
  --jtn-space-common: 13px;
  --jtn-border-radius-small: 4px;
  --jtn-border-radius-large: 12px;
  --jtn-dialog-width: 240px;
  --jtn-padding-y-common: 10px;
  --jtn-padding-x-common: 16px;
  --jtn-color-bg: #fff;
  --jtn-color-text: #333;
  --jtn-color-primary: #1f4de3;
  --jtn-gray-50: #f3f3f3;
  --jtn-gray-100: #e6e6e6;
  --jtn-gray-200: #cdcdcd;
  --jtn-color-border-common: rgba(0, 0, 0, 0.1);
  --jtn-color-highlight: rgba(0, 0, 0, .2);
  --jtn-transition-ease: cubic-bezier(0.85, 0, 0.15, 1);
  --jtn-transition-speed: 200ms;
  --jtn-box-shadow-shallow: 3px 5px 10px rgba(0, 0, 0, .13);
  --jtn-box-shadow-deep: 3px 5px 25px rgba(0, 0, 0, .18);
  --jtn-filter-drop-shadow-deep: 3px 5px 15px rgba(0, 0, 0, .18);
}

.jump-to-nav {
  position: fixed;
  top: 100px;
  right: 18px;
  pointer-events: none;
  font-size: var(--jtn-base-font-size) !important;
  filter: drop-shadow(var(--jtn-filter-drop-shadow-deep));
}
.jump-to-nav * {
  -webkit-font-smoothing: auto;
}
.jump-to-nav *,
.jump-to-nav :after,
.jump-to-nav :before {
  box-sizing: border-box;
}
.jump-to-nav .jump-to-nav__dialog {
  overflow: hidden;
  width: var(--jtn-dialog-width);
  height: auto;
  transition: clip-path var(--jtn-transition-speed) var(--jtn-transition-ease);
  color: var(--jtn-color-text);
  border-radius: var(--jtn-border-radius-large);
  background-color: var(--jtn-color-bg);
  font-family: var(--jtn-ff-primary);
  clip-path: inset(0 0 calc(100% - var(--jtn-idle-width)) calc(100% - var(--jtn-idle-width)) round var(--jtn-border-radius-large));
}
.jump-to-nav .jump-to-nav__dialog > *:not(.jump-to-nav__maximize) {
  visibility: hidden;
  transition: visibility var(--jtn-transition-speed) var(--jtn-transition-ease);
  transition-delay: var(--jtn-transition-speed);
  pointer-events: none;
}
.jump-to-nav .jump-to-nav__dialog > *:not(.jump-to-nav__maximize) a:focus,
.jump-to-nav .jump-to-nav__dialog > *:not(.jump-to-nav__maximize) button:focus {
  outline: 0;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__maximize {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--jtn-idle-width);
  height: var(--jtn-idle-width);
  cursor: pointer;
  pointer-events: auto;
  border: 0;
  background-color: var(--jtn-color-bg);
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__maximize svg {
  position: relative;
  right: 1px;
  width: 9px;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__minimize {
  width: 28px;
  height: 28px;
  margin-left: auto;
  padding: 0;
  cursor: pointer;
  border: 0;
  border-radius: 30px;
  background-color: transparent;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__minimize:hover {
  background-color: var(--jtn-gray-100);
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__link {
  display: inline-block;
  margin-bottom: 8px;
  text-decoration: none;
  color: var(--jtn-color-primary);
  font-size: var(--jtn-small-font-size);
  font-weight: normal;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__list,
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__nested-list {
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
  line-height: 1.2;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__list {
  margin-top: 0;
  margin-left: -10px;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__item {
  padding-left: 10px;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__item:has(.jump-to-nav__item--active) > .jump-to-nav__link {
  font-weight: 500;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  min-height: 50px;
  padding: var(--jtn-padding-y-common) var(--jtn-padding-x-common) 0 var(--jtn-padding-x-common);
  border-bottom: 1px solid var(--jtn-color-border-common);
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__body {
  padding: var(--jtn-padding-y-common) var(--jtn-padding-x-common);
  border-right: 1px solid var(--jtn-color-bg);
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__body::-webkit-scrollbar-corner {
  background-color: transparent;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__body::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__body::-webkit-scrollbar-track {
  background-color: transparent;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__body::-webkit-scrollbar-thumb {
  outline: 0;
  background-color: var(--jtn-gray-100);
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__body::-webkit-scrollbar-thumb:hover {
  background-color: var(--jtn-gray-200);
}
.jump-to-nav .jump-to-nav__dialog [role=heading] {
  font-weight: bold;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: antialiased;
}
.jump-to-nav .jump-to-nav__dialog [role=heading][aria-level="1"] {
  font-size: 17px;
}
.jump-to-nav .jump-to-nav__dialog [role=heading][aria-level="2"] {
  margin-bottom: 8px;
  font-size: 14px;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__item--active > .jump-to-nav__link {
  font-weight: bold;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__search {
  position: relative;
  flex: 0 0 100%;
  width: 100%;
  margin: var(--jtn-space-common) 0;
  font-size: var(--jtn-small-font-size);
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__search.jump-to-nav__search--loading {
  visibility: hidden;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__search.jump-to-nav__search--has-value .jump-to-nav__search-clear {
  display: flex;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__search-icon {
  position: absolute;
  top: 50%;
  left: 12px;
  width: 12px;
  height: 12px;
  transform: translateY(-50%);
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__search-icon path {
  fill: var(--jtn-color-text);
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__search-clear {
  position: absolute;
  top: 2px;
  right: 2px;
  width: 30px;
  height: calc(100% - 4px);
  background-color: var(--jtn-gray-50);
  background: linear-gradient(90deg, rgba(243, 243, 243, 0) 0, var(--jtn-gray-50) 25%);
  display: none;
  align-items: center;
  justify-content: center;
  padding: 0;
  cursor: pointer;
  border: 0;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__search-clear svg {
  width: 14px;
  height: 14px;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__search-clear path {
  fill: var(--jtn-color-text);
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__search-input[type=text] {
  width: 100%;
  margin: 0;
  padding: var(--jtn-padding-y-common) 2px var(--jtn-padding-y-common) 32px;
  border: 0;
  border-radius: var(--jtn-border-radius-small);
  background-color: var(--jtn-gray-50);
  box-shadow: none;
  font-family: var(--jtn-ff-primary);
  font-size: var(--jtn-small-font-size);
  line-height: 1;
}
.jump-to-nav .jump-to-nav__dialog .jump-to-nav__search-input[type=text]:focus {
  outline-color: var(--jtn-color-primary);
}
.jump-to-nav .jump-to-nav__dialog .autoComplete_wrapper {
  position: relative;
  flex: 0 0 100%;
  width: 100%;
}
.jump-to-nav .jump-to-nav__dialog .autoComplete_wrapper > ul {
  position: absolute;
  z-index: 10;
  top: calc(100% + 5px);
  left: 0;
  overflow: auto;
  width: 100%;
  max-height: 160px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  border: 1px solid var(--jtn-color-border-common);
  border-radius: var(--jtn-border-radius-small);
  background-color: var(--jtn-color-bg);
  box-shadow: var(--jtn-box-shadow-shallow);
}
.jump-to-nav .jump-to-nav__dialog .autoComplete_wrapper li {
  padding: calc(var(--jtn-padding-x-common) / 2) var(--jtn-padding-x-common);
  border-bottom: 1px solid var(--jtn-gray-100);
  font-size: var(--jtn-small-font-size);
  font-weight: bold;
  line-height: 1.4;
}
.jump-to-nav .jump-to-nav__dialog .autoComplete_wrapper li:last-child {
  border-bottom: 0;
}
.jump-to-nav .jump-to-nav__dialog .autoComplete_wrapper mark {
  padding: 0;
  background-color: var(--jtn-gray-100);
}
.jump-to-nav .jump-to-nav__dialog .autoComplete_wrapper[aria-expanded=true] li:not([aria-selected=true]):first-child {
  background-color: var(--jtn-gray-50);
}
.jump-to-nav .jump-to-nav__dialog .autoComplete_wrapper [aria-activedescendant*=autoComplete_result_] + ul li:not([aria-selected=true]):first-child {
  background-color: transparent;
}
.jump-to-nav .jump-to-nav__dialog .autoComplete_wrapper [aria-selected=true] {
  color: var(--jtn-color-bg);
  background-color: var(--jtn-color-primary);
}
.jump-to-nav .jump-to-nav__dialog .autoComplete_wrapper [aria-selected=true] mark {
  color: var(--jtn-color-bg);
  background-color: var(--jtn-color-highlight);
}
.jump-to-nav.jump-to-nav--showing {
  pointer-events: auto;
}
.jump-to-nav.jump-to-nav--showing .jump-to-nav__dialog {
  clip-path: inset(0 0 0 0 round var(--jtn-border-radius-large));
}
.jump-to-nav.jump-to-nav--showing .jump-to-nav__dialog > * {
  visibility: visible;
  transition-delay: 0ms;
  pointer-events: auto;
}
.jump-to-nav.jump-to-nav--showing .jump-to-nav__dialog > * a:focus,
.jump-to-nav.jump-to-nav--showing .jump-to-nav__dialog > * button:focus {
  outline: auto;
}
.jump-to-nav.jump-to-nav--showing .jump-to-nav__dialog .jump-to-nav__maximize {
  display: none;
}
.jump-to-nav.jump-to-nav--showing .jump-to-nav__dialog .jump-to-nav__body {
  overflow: auto;
}